import React from 'react'

import './Render-input.css'

const RenderInput = ({ input, type, label, meta: { touched, error }, isSmall, ...props }) => {
  const wrapperCls = ['form-group']
  const inputCls = ['form-control']
  const isError = (touched && error)
  const id = Math.trunc(Math.random() * 1000)
  if (isError) {
    wrapperCls.push('has-danger')
    inputCls.push('is-invalid')
  }
  if (isSmall) {
    inputCls.push('form-control-sm')
  }
  return (
    <div className={wrapperCls.join(' ')}>
      <label htmlFor={`password-${id}`}>{label}</label>
      <input 
        type={type} 
        className={inputCls.join(' ')}
        id={`${type}-${id}`}
        {...input} {...props} />
      {isError && <div className="invalid-feedback">{error}</div>}
    </div>
  )
}

export default RenderInput
