import React from 'react'
import { Button } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import { Field, reduxForm } from 'redux-form'
import { RenderInput } from '../../UI/inputs'
import { required, minLength, passwordMatched } from '../../../validators/validators'

const minLength6 = minLength(6)

const RegisterForm = ({ handleSubmit }) => {
  return (
    <form onSubmit={handleSubmit}>
      <Field
        component={RenderInput}
        type="text"
        label="Логин"
        name="login"
        validate={required} />
      <Field
        component={RenderInput}
        type="email"
        label="Эл. почта"
        name="email"
        validate={required} />
      <Field
        component={RenderInput}
        type="password"
        label="Пароль"
        name="password"
        validate={[required, minLength6]} />
      <Field
        component={RenderInput}
        type="password"
        label="Повторите пароль"
        name="repeatedPassword"
        validate={[required, minLength6, passwordMatched]} />
      <div className="d-flex flex-column justify-content-center align-items-center">
        <Button type="submit" className="btn btn-primary mb-3">Зарегистрироваться</Button>
        Уже есть аккаунт?
        <Link
          to="/auth/login"
          className="btn btn-secondary mt-3">
          Войти
          </Link>
      </div>
    </form>
  )
}

export default reduxForm({
  form: 'register'
})(RegisterForm)
