import React from 'react'

const RenderSelect = ({ input, label, meta: { touched, error }, isSmall, ...props }) => {
  const selectCls = ['form-control']
  const isError = (touched && error)
  const id = Math.trunc(Math.random() * 1000)
  if (isError) {
    selectCls.push('is-invalid')
  }

  if (isSmall) {
    selectCls.push('form-control-sm')
  }

  return (
    <div 
      className={isError ? 'form-group has-danger' : 'form-group'}>
      <label htmlFor={id}>{label}</label>
      <select 
        className={selectCls.join(' ')} 
        id={id} onChange={input.onChange}>
        {props.children}
      </select>
      {isError && <div className="invalid-feedback">{error}</div>}
    </div>
  )
}

export default RenderSelect