import React from 'react'

const RenderTextarea = ({ input, label, ...props }) => {
  const id = Math.trunc(Math.random() * 1000)

  return (
    <div className="form-group">
      <label htmlFor={id}>{label}</label>
      <textarea className="form-control" id={id} rows="5" {...input} />
      <small className="form-text text-muted">{props.mutedText}</small>
    </div>
  )
}

export default RenderTextarea