import React from 'react'
import { Alert } from 'react-bootstrap'
import { Redirect, withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import Login from '../components/Login/Login'
import Register from '../components/Register/Register'
import { signIn, signUp, clearAuthError } from '../reducers/action-creators/authorization-AC'

class AuthContainer extends React.Component {
  componentWillUnmount = () => {
    this.props.clearAuthError()
  }

  componentDidUpdate(prevProps) {
    if (prevProps.match.params.mode !== this.props.match.params.mode) {
      this.props.clearAuthError()
    }
  }

  onSignIn = (credentials) => {
    this.props.signIn(credentials)
  }

  onSignUp = (userData) => {
    this.props.signUp(userData)
  }

  render() {
    const { mode } = this.props.match.params
    const { auth, error } = this.props
    if (auth.uid) {
      return <Redirect to="/" />
    }
    return (
      <div className="form-wrapper" style={{ maxWidth: "450px", margin: "30px auto" }}>
        {error
          ? (
            <Alert
              className="alert alert-danger">
              <Alert.Heading>{error}</Alert.Heading>
              <p>Пожалуйста, проверьте данные и попробуйте еще раз</p>
            </Alert>
          )
          : null}
        {mode === 'login'
          ? (<Login onSubmit={this.onSignIn} />)
          : (<Register onSubmit={this.onSignUp} />)
        }
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    error: state.authorization.error,
    auth: state.firebase.auth
  }
}

const mapDispatchToProps = {
  signIn,
  signUp,
  clearAuthError
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(AuthContainer))
